<!-- 头部四个链接页面 -->
<template>
  <div class="tab">
    <!-- to里面直接写字符串也可以? -->
    <router-link tag="div" class="tab-item" to="/release-now">
      <span class="tab-link">正在热映</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/release-future">
      <span class="tab-link">即将上映</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/top250">
      <span class="tab-link">top250</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/search">
      <span class="tab-link">影视搜索</span>
    </router-link>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {}
</script>

<style  lang="css">
.tab {
  display: flex;
  height: 44px;
  line-height: 44px;
  font-size: 14px;
}
.tab-item {
  flex: 1;
  text-align: center;
}
.tab-link {
   padding-bottom: 5px;
   color: #666;
}
.router-link-active .tab-link{
   color:#ffcd32;
   border-bottom: 2px solid #ffcd32;
}

</style>


