<template>
  <ul class="switches">
    <li class="switch-item"
        v-for="(item,index) in switches"
        :class="{'active':currentIndex === index, 'small-font':smallFont === true}"
        @click="switchItem(index)">
      <span>{{item}}</span>
    </li>
  </ul>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      switches: {
        type: Array,
        default: []
      },
      currentIndex: {
        type: Number,
        default: 0
      },
      smallFont: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      switchItem(index) {
        this.$emit('switch', index)
      }
    }
  };
</script>

<style  lang="css">
.switches {
    position: relative;
    z-index: 200;
    background: #ccc;
    display: flex;
    align-items: center;
    width: 100%;
}
   
.switch-item {
    flex: 1;
    text-align: center;
    padding: 15px;
    font-size: 14px;
    color: #000;
    border-bottom: 1px solid transparent;
} 
.switch-item.small-font {
     padding: 14px 5px;
     font-size: 12px;
}
       
.switch-item.active {
    color: green;
    border-bottom: 1px solid #fff;
}
        
</style>



