<template lang="html">
    <div>
        <release-list @select="selectItem" :urlX="urlX"></release-list>
        <router-view></router-view>
    </div>
</template>

<script>
import ReleaseList from 'components/release-list/release-list'
import {mapMutations} from 'vuex'
export default {
    data() {
        return {
            urlX: 'in_theaters'
        }
    },
    methods: {
        selectItem(movie,index) {
            setTimeout(() => {
                this.$router.push({
                    path: `/release-now/${movie.id}`
                 })
            },500)
            this.setFirstRouter('release-now')
        },
        ...mapMutations({
            setFirstRouter: 'SET_FIRST_ROUTER'
        })
    },
    components: {
        ReleaseList
    }
}
</script>

<style lang="css">

</style>


