<template>
   <div class="suggest-wrapper" v-if="query && searchList.length"> 
    <scroll class="suggest" :data="searchList" :pullup="pullup" :beforeScroll="beforeScroll"
    @beforeScroll="listScroll"
    @scrollToEnd="searchMoreResult"　ref="suggest" > 
        <div class="suggest-list-scroll"> 
            <ul class="suggest-list">
                <li class="suggest-item" v-for="item in searchList"  @click="selectItem(item)">
                <div class="item-pic">
                    <img v-lazy="item.images.small" width="30" height="30">
                </div>
                <div class="item-info">
                    <div class="title">{{item.title}}</div>
                </div>
                </li> 
            </ul>
            <loading v-show="hasMore"></loading>
        </div>
    </scroll>
   </div> 
</template>

<script type="text/ecmascript-6">
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
export default {
    props: {
      query: {
        type: String,
        default: ''
      },
      searchList: {
          type: Array,
          default: ''
      },
      hasMore: {
        type: Boolean,
        default: false
      },
      tagSearch: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        pullup: true,
        beforeScroll: true
      }
    },
    methods: {
        selectItem(item) {
            this.$emit('selectItem' ,item.id)
        },
        searchMoreResult() {
            this.$emit('searchMoreResult')
        },
        listScroll() {
            this.$emit('listScroll')
        }
    },
    components: {
        Scroll,
        Loading
    }
}
</script>

<style lang="css">
.suggest-wrapper {
    position: fixed;
    top: 145px;
    bottom: 0;
    left: 0;
    right: 0;
}
.suggest {
    height: 100%;
    overflow: hidden;
    /* background: #7EC0EE; */
    background: #ccc;
}


.suggest-list {
    padding: 15px;
}

.suggest-list   .suggest-item {
    padding: 5px;
    height: 46px;
    display: flex;
    border-bottom: 1px solid #fff;
}

.suggest-list  .item-pic {
    flex: 30px 0 0;
    width: 50px;
    margin-right: 10px;
}
.suggest-list  .item-pic img {
    border-radius: 50%;
}       
.suggest-list  .item-info {
    flex: 1;
    padding-bottom: 30px;
}

.suggest-list  .title {
    font-size: 16px;
    line-height: 36px;
    color: #000;
    margin-bottom: 10px;
}
    
.suggest-list  .info {
    font-size: 0;
}
    
.suggest-list  .info   span {
    font-size: 12px;
}
    
.no-result {
    position: absolute;
    top: 50%;
    width: 100%;
    text-align: center;
}

</style>



