<template>
  <div class="movie-comment">
    <div class="type-title" v-if="needTitle">
      短评{{commentNum}}条
    </div>
    <ul>
      <li v-for="(comment,index) in comments" class="comment-item">
        <div class="avatar">
          <img v-lazy="comment.author.avatar" width="36" height="36">
        </div>
        <div class="content">
          <h1 class="name">{{comment.author.name}}</h1>
          <star :size="24" :score="comment.rating.value * 2"></star> 
          <p class="text">{{comment.content}}</p>
          <span class="date">{{comment.created_at}}</span>
          <div class="useful-count">
            <i class="icon-thumb_up" ></i>
           
          </div>
        </div>
      </li>
    </ul>
    <div class="allComment" @click="needAllComments" v-if="!needTitle">
      <span>全部短评{{commentNum}}个</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Star from 'base/star/star';
  export default {
    props: {
      comments: {
        type: Array,
        default: function() {
          return []
        }
      },
      commentNum: {
        type: Number,
        default: 0
      },
      needTitle: {
        type: Boolean,
        default: false
      },
      hasMore: {
        type: Boolean,
        default: true
      }
    },
    methods: {
      needAllComments() {
        this.$emit('needAllComments');
      }
    },
    components: {
      Star
    }
  };
</script>

<style lang="css">
.movie-comment {
    padding: 20px;
    background: #ccc;
}
.type-title {
    font-size: 14px;
    margin-bottom: 15px;
}  
.comment-item {
    display: flex;
    margin-bottom: 20px;
    border-bottom: 1px solid #fff;
}
.avatar {
    flex: 0 0 36px;
    width: 36px;
    margin-right: 12px;
}
       
.movie-comment img {
     border-radius: 50%;
}
.movie-comment .content {
    flex: 1;
    position: relative;
    font-size: 12px;
    color: #000;
}
.movie-comment .content h1 {
   font-size: 14px;
}
.movie-comment .content .text {
    font-size: 12px;
    color: #333;
}
.movie-comment .name {
    display: inline-block;
    line-height: 25px;
    margin-right: 5px;
}
.star {
    display: inline-block;
}
.text {
    line-height: 20px;
}
.date {
    font-size: 12px;
    color: #000;
    line-height: 25px;
}
.useful-count {
    position: absolute;
    color: #000;
    top: 5px;
    right: 5px;
}
.useful-count.like {
     color: #fff;
}               
.allComment {
    font-size: 14px;
    line-height: 28px;
    border-radius: 14px;
    color: #fff;
    text-align: center;
    background: #8af27c;
}
      
</style>



