import Vue from 'vue'
import Router from 'vue-router'

import ReleaseNow from 'components/release-now/release-now'
import ReleaseFuture from 'components/release-future/release-future'
import Top250 from 'components/top250/top250'
import Search from 'components/search/search'
import Detail from 'base/detail/detail'
import ActorDetail from 'base/actor-detail/actor-detail'
import ReviewDetail from 'base/review-detail/review-detail'
import Discussion from 'base/discussion/discussion'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/release-now'
    },{
      path: '/release-now',
      component: ReleaseNow,
      children: [
          {
              path: ':id',
              component: Detail
          }
      ]
    },{
      path: '/release-future',
      component: ReleaseFuture,
      children: [
          {
              path: ':id',
              component: Detail
          }
      ]
    },{
      path: '/top250',
      component: Top250,
      children: [
          {
              path: ':id',
              component: Detail
          }
      ]
    },{
      path: '/search',
      component: Search,
      children: [
          {
              path: ':id',
              component: Detail
          }
      ]
    },{
      path: '/celebrity/:celebrityId',
      component: ActorDetail
    },{
      path: '/review/:reviewId',
      component: ReviewDetail
    },{
      path: '/discussion/:discussionId',
      component: Discussion
    }
  ]
})



