<template lang="html">
   <scroll 
    :pullup="pullup" :data="releaseNowList"
    @scrollToEnd="searchMore"
    class="release" ref="scroll">
       <listview @select="selectItem" :movies="releaseNowList" :loading="hasMore"></listview>
    </scroll>
</template>

<script>
import Scroll from 'base/scroll/scroll'
import Listview from 'base/listview/listview'
import {getList} from 'api/release' 
export default {
    props: {
        // urlX由外部传来的字符串拼成一个整的地址
        urlX: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            releaseNowList: [],
            hasMore: true,
            pullup: true,
            // 其实搜索位置
            start: 0,
            // 是否显示加载更多
            loading: true
        }
    },
   created() {
        this._getList()
   },
   methods: {
        selectItem(movie,index) {
            this.$emit('select', movie, index)
        },
        _getList() {
            //urlX拼接地址用的    
            getList(this.start, this.urlX).then((res) => {
                this.releaseNowList = res.subjects
                this._checkMore(res) 
            })
        },
         // 检测有没有多余的
        _checkMore(data) {
            const list = data.subjects
            // 20是默认一次发20条的
            this.start = data.start+data.count
            if(!list.length || this.start >= data.total) {
                this.hasMore = false
            }
        },
        // 滚到底部触发
        searchMore() {
            // 没有更多了直接返回
            if(!this.hasMore) {
                return
            }
            getList(this.start, this.urlX).then((res) => {
                // 拼接
                this.releaseNowList = this.releaseNowList.concat(res.subjects)
               
                this._checkMore(res)
            })
        }
   },
   components: {
       Scroll,
       Listview
   }
}
</script>

<style lang="css">
.release {
    z-index: -1;
    position: fixed;
    width: 100%;
    top: 88px;
    bottom: 0;
    background: #ddd;
}
</style>


