
<template>
  <div class="movie-review">
    <div class="type-title" v-if="needTitle">
      影评{{reviewNum}}条
    </div>
    <ul>
      <li v-for="review in reviews" class="review-item" @click="selectReview(review.id)">
        <div class="content">
          <h1 class="title">{{review.title}}</h1>
          <div class="user-info">
            <div class="avatar">
              <img v-lazy="review.author.avatar" alt="" width="25" height="25">
            </div>
            <span class="name">{{review.author.name}}</span>
            <star :size="24" :score="review.rating.value*2"></star>
          </div>
          <p class="desc">{{review.summary}}</p>
        </div>
      </li>
    </ul>
    <div v-if="!needTitle" class="allReview" @click="needAllReviews">
      <span>全部影评{{reviewNum}}个</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Star from 'base/star/star';
  export default {
    props: {
      reviews: {
        // types: Array,
        default: []
      },
      reviewNum: {
        type: Number,
        default: 0
      },
      needTitle: {
        type: Boolean,
        default: false
      },
      hasMore: {
        type: Boolean,
        default: true
      }
    },
    methods: {
      // 具体影评
      selectReview(id) {
        this.$emit('selectReview', id);
      },
      // 全部影评
      needAllReviews() {
        this.$emit('needAllReviews');
      }
    },
    components: {
      Star
    }
  };
</script>

<style lang="css">
.movie-review {
    padding: 20px;
    background: #ccc;
}
.movie-review .type-title {
    font-size: 14px;
    margin-bottom: 15px;
}
.movie-review .review-item {
     margin-bottom: 10px;
     padding-bottom: 10px;
     border-bottom: 1px solid #fff;
}
.movie-review .content {
     font-size: 12px;
}   
.movie-review .content h1 {
     color: green;
} 
.movie-review .title {
    font-size: 14px;
    color: #fff;
    padding: 5px 0
}
.movie-review .user-info {
    padding: 5px 0;
}
.movie-review .avatar {
    display: inline-block;
    vertical-align: middle;
    margin-right: 6px;
}
.movie-review img {
    border-radius: 50%;
}
.movie-review .name {
    display: inline-block;
    vertical-align: middle;
}
.movie-review .star {
    display: inline-block;
    vertical-align: middle;
}   
.movie-review .desc {
    line-height: 20px;
}     
.allReview {
    font-size: 14px;
    line-height: 28px;
    border-radius: 14px;
    color: #fff;
    text-align: center;
    background: #8af27c;
}
   
</style>



