<template lang="html">
  <div class="review-wrapper" @touchstart.stop @click.stop>
    <div class="fixed-title">
      <div class="back"  @click="back">
          <span class="icon-back"><</span>
      </div>
      <span class="type">影评</span>
    </div>
    <scroll class="review-detail" :data="scrollData" ref="scroll">
      <div class="scroll-wrapper">
        <div class="review-content" v-if="reviewDetail.author">
          <h1 class="title">{{reviewDetail.title}}</h1>
          <div class="author">
            <div class="avatar">
              <img :src="reviewDetail.author.avatar" alt="" height="36" width="36">
            </div>
            <div class="info">
              <h2><span class="name">{{reviewDetail.author.name}}</span>的影评</h2>
              <span class="date">{{reviewDetail.created_at.split(' ')[0]}}</span>
              <star :size="24" :score="reviewDetail.rating.value*2"></star>
            </div>
          </div>
          <p class="desc" v-html="reviewDetail.content"></p>
          <div class="copyright">
            本文版权归 {{reviewDetail.author.name}} 所有，任何形式转载请联系作者
          </div>
          <div class="end">
            <div class="line"></div>
            <span class="text">THE END</span>
          </div>
          <div class="useful">
            <div class="use">
              有用({{reviewDetail.useful_count}})
            </div>
            <div class="no-use">
              没用({{reviewDetail.useless_count}})
            </div>
          </div>
        </div>
      </div>
    </scroll>
  </div>
</template>

<script>
import Star from 'base/star/star'
import Scroll from 'base/scroll/scroll'
import {getDetail} from 'api/getDetail'
import {mapGetters, mapMutations} from 'vuex'
export default {
  data() {
    return {
      reviewDetail: {},
      scrollData: []
    }
  },
  methods: {
    back() {
      this.$router.back()
      this.$destroy()
    }
  },
  created() {
      console.log(this.$route.params.reviewId)
      getDetail(`review/${this.$route.params.reviewId}`).then((res) => {
          this.reviewDetail = res
      })
      // 被逼无奈写的滚动的数据
      getDetail(`celebrity/1350194`).then((res) => {
          this.scrollData = res.photos
        //   this.$refs.scroll.refresh()
      })
  },
  mounted() {
   
  },
  computed: {
     
  },
  components: {
    Scroll,
    Star
  },
  watch: {
    scrollData() {
        this.$refs.scroll.refresh()   
    }
  }
}
</script>

<style lang="css">
.review-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  background-color: #ccc;
} 
.review-wrapper .review-scroll {
   position: relative;
   padding: 20px;
}
.fixed-title {
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    z-index: 200;
    background-color: #ccc; 
}
.fixed-title .back {
    height: 50px;
    width: 50px;
    position: absolute;
    top: 0;
    left: 10px;
    z-index: 50;
    font: bolder 40px/50px "宋体";
    text-align: center;
    color: #fff;
}
.fixed-title .icon-back {
    
}
.fixed-title .type {
    line-height: 50px;
    font-size: 20px;
    color: green;
    padding-left: 100px;
}
.review-detail {
     position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 150;
    overflow: hidden;
    background-color: #ccc; 
}
.review-detail  .review-content {
    padding: 20px 20px 40px 20px;
}
.review-detail  .title {
    font-size: 16px;
    color: #000;
    line-height: 30px;
}
.review-detail  .author {
    margin-top: 20px;
    display: flex;
    align-items: top;
}
.review-detail  .avatar {
    flex: 36px 0 0;
    width: 36px;
}
.review-detail  img {
    border-radius: 50%;
}
.review-detail  .info {
    flex: 1;
    padding-left: 10px;
    font-size: 14px;
}
.review-detail  h2 {
    padding-bottom: 5px;
}
.review-detail  .name {
    color: #000;
    padding-right: 5px;
}
.review-detail  .star {
    display: inline-block;
}
.review-detail  .desc {
    margin-top: 30px;
    color: #000;
    line-height: 25px;
    white-space: pre-wrap;
}
.fixed-title .copyright {
    margin-top: 40px;
    font-size: 12px;
    text-align: center;
}
.fixed-title .end {
    position: relative;
    margin: 30px auto;
    text-align: center;
    height: 10px;
    width: 60%;
}
.fixed-title .line {

}
.fixed-title .text {
    padding: 0 10px;
    display: inline-block;
    background-color: #ccc;
    position: absolute;
    left: 50%;
    top: -8px;
    transform: translateX(-50%);
    font-size: 14px;
}
.fixed-title .useful {
    text-align: center;
}
.fixed-title .useful  div {
    display: inline-block;
    padding: 10px 15px;
    margin-right: 10px;
    color: #000;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 5px;
}
          

</style>


