<!-- 所有可以滚动的，划屏的组件-->
<template>
  <!-- 剧照 -->
    <div class="stage-photo-wrapper" v-if="stagePhotoAll.length">
        <h3>部分剧照</h3> 
        <scroll :data="stagePhotoAll" :scrollX="true" :scrollY="false">
            <div class="stage-photo-scroll-wrapper" :style="getStageScrollWidth">       
                <div class="stage-photo" v-for="stagePhoto in stagePhotoAll">
                    <img v-lazy="stagePhoto.medium" height="160" width="200">
                </div>
            </div>
        </scroll>
    </div>
</template>

<script type="text/ecmascript6">
import Scroll from 'base/scroll/scroll'
const STAGE_PHOTO_WIDTH = 220
export default {
    props: {
        stagePhotoAll: {
            type: Array,
            default: []
        }
    },
    computed: {
        getStageScrollWidth() {
            if(this.stagePhotoAll.length) {
                let w = STAGE_PHOTO_WIDTH*this.stagePhotoAll.length
                return `width:${w}px`
                console.log(w)
            }
        }
    },
    components: {
        Scroll
    }
}
</script>

<style lang="css">
.stage-photo-wrapper {
    padding: 20px;
}
.stage-photo {
    padding: 10px;
    display: inline-block;
    text-align: center;
}
</style>




