<template>
  <div class="discussion-wrapper">
    
      <div class="fixed-title">
        <span class="back" @click="back">
          <
        </span>
          <span class="type">{{title}}</span>
          <span class="to-top" @click="toTop">
          <i class="icon-circle-up"></i>
        </span>
      </div>
      <scroll class="all-discuss"
              :data="discussions"
              ref="scroll"
              :pullup="pullup" 
              @scrollToEnd="searchMore"
      >
        <div class="discuss-content">
          <movie-comment
            v-if="currentDiscussionType==='comments'"
            :comments="discussions"
            :commentNum="discussionNum"
          >
          </movie-comment>
          <movie-review
            v-else
            :reviews="discussions"
            :reviewNum="discussionNum"
            @selectReview="selectReview"
          >
          </movie-review>
          <loading v-show="hasMore"></loading>
        </div>
      </scroll>
    </div>
</template>

 <script type="text/ecmascript-6">
  import Loading from 'base/loading/loading'
  import MovieComment from 'base/movie-comment/movie-comment'
  import MovieReview from 'base/movie-review/movie-review'
  import Scroll from 'base/scroll/scroll'
  import { mapGetters } from 'vuex'
  import { getAllComment } from 'api/getAllComment'

  const SEARCH_MORE = 20;

  export default {
    data() {
      return {
        discussions: [],
        discussionNum: 0,
        pullup: true, // 支持滚动加载
        hasMore: true, // 存在更多数据
        // 数据分页
        start: 0
      };
    },
    computed: {
      title() {
        return `全部${this.type}`;
      },
      type() {
        return this.currentDiscussionType === 'comments' ? '短评' : '影评';
      },
      ...mapGetters([
        'currentDiscussionType'
      ])
    },
    created() {
      // 获取数据
      this._getData()
    },
    methods: {
      back() {
        this.$router.back()
        this.$destroy()
      },
      toTop() { // 返回顶部
        this.$refs.scroll.scrollTo(0, 0, 500);
      },
      _getData() {
        getAllComment(this.start,`${this.$route.params.discussionId}/${this.currentDiscussionType}`).then((res) => {
          // console.log(res)
          this.discussions = res[this.currentDiscussionType]
          this.discussionNum = res.total
          this._checkMore(res)
        })
      },
      // 检测有没有多余的
      _checkMore(data) {
            const list = data[this.currentDiscussionType]
            // 20是默认一次发20条的
            this.start = data.start+data.count
            if(!list.length || this.start >= data.total) {
                this.hasMore = false
            }
        },
        // 滚到底部触发
        searchMore() {
            // 没有更多了直接返回
            if(!this.hasMore) {
                return
            }
            getAllComment(this.start,`${this.$route.params.discussionId}/${this.currentDiscussionType}`).then((res) => {
                console.log(res)
                this.discussions = this.discussions.concat(res[this.currentDiscussionType])
                this._checkMore(res)
            })
        },
        // 调用具体的影评
        selectReview(id) {
            this.$router.push({
                path: `/review/${id}`
            })
        }
    },
    components: {
      MovieComment,
      MovieReview,
      Scroll,
      Loading
    }
  };
</script>

<style lang="css">
.discussion-wrapper {
  height: 100%;
}

.discussion-wrapper  .fixed-title {
  position: fixed;
  top: 0;
  width: 100%;
  height: 50px;
  z-index: 200;
  background-color: #ccc;
}

.discussion-wrapper  .back {
  position: absolute;
  top: 0px;
  left: 6px;
  z-index: 50;
}

.discussion-wrapper .icon-back {
  display: block;
  padding: 15px;
  font-size: 18px;
  color: #ccc;
}
  
.discussion-wrapper .type {
  line-height: 50px;
  font-size: 14px;
  padding-left: 50px;
}

.discussion-wrapper .to-top {
  position: absolute;
  top: 0;
  right: 6px;
  z-index: 50;
}

.discussion-wrapper .icon-circle-up {
  display: block;
  padding: 15px;
  font-size: 16px;
  color: #000;
}
  
.discussion-wrapper .all-discuss {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 150;
  overflow: hidden;
  background-color: #ccc;
}
.discussion-wrapper  .discuss-content {
  padding-top: 50px
}

.discussion-wrapper  .no-result {
  position: absolute;
  top: 50%;
  width: 100%;
  text-align: center;
}
</style>



