<template>
    <div class="work-wrapper" @click.stop>
        <h3>{{title}}</h3> 
        <scroll :data="works" :scrollX="true" :scrollY="false" :click="false" ref="scroll">
            <div class="actors-scroll-wrapper"  :style="getScrollWidth"> 
                <div class="actor" v-for="work in works" @click="selectItem(work)">
                    <img v-lazy="work.subject.images.medium" height="160" width="120">
                    <p class="movie-name-text">{{work.subject.title}}</p>
                </div>
            </div>
        </scroll>
    </div>
</template>

<script type="text/ecmascript6">
import Scroll from 'base/scroll/scroll'
const ACTOR_WIDTH = 160
export default {
    props: {
        works: {
            type: Array,
            default: []
        },
        title: {
            type: String,
            default: '代表作品'
        }
    },
    computed: {
        getScrollWidth() {
            if(this.works.length) {
                let w = ACTOR_WIDTH*this.works.length
                return `width:${w}px`
            }
        }
    },
    methods: {
        selectItem(work) {
            // console.log(work.subject.id)
            this.$emit('select',work.subject)
            setTimeout(() => {
                 this.$refs.scroll.scrollTo(0,0)
            },1000)
           
        }
    },
    components: {
        Scroll
    }
}
</script>

<style lang="css">
.work-wrapper {
    margin-top: 20px;
}
.actor {
    vertical-align: top;
}
.movie-name-text {
    width: 120px;
}
</style>




