<template>
  <div class="movie-list">
    <ul>
        <li @click="selectItem(movie,index)" v-for="(movie,index) in movies" class="item">
            <div class="pic">
                <img v-lazy="movie.images.medium" width="110" height="140">
            </div>
            <div class="content">
                <h2 class="name">{{movie.title}}</h2>
                <p class="desc">{{_arrayJointType(movie.genres, '-')}}</p>
                <p class="desc">导演：{{_arrayJointActor(movie.directors, '/')}}</p>
                <p class="desc">主演：{{_arrayJointActor(movie.casts, '/')}}</p>
                <div class="score">
                    <p class="desc">评分：</p>
                    <star :score="Math.round(movie.rating.average)"></star>
                    <p>{{_score(movie.rating.average)}}</p>
                </div>
                
                <p class="desc">{{movie.collect_count}}人看过</p>
            </div>
        </li>
        <loading v-show="loading"></loading>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
import {arrayJoint} from 'common/js/util'
import Loading from 'base/loading/loading'
import Star from 'base/star/star'
export default {
    props: {
        movies: {
            type: Array,
            default: []
        },
        loading: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        selectItem(movie,index) {
            this.$emit('select', movie, index)
        },
        _arrayJointType(arr, jointSymbol) {
            if(this.movies.length) {
                return arrayJoint(arr, jointSymbol)
            }
            return ''
        },
        _arrayJointActor(arr, jointSymbol) {
            let arrActor = arr.map((value) => {
                return value.name
            })
            
            if(arrActor) {
                return arrayJoint(arrActor, jointSymbol)
            }
            return ''
        },
        _score(num) {
            if(num == 0) {
                return '暂无评分'
            }
            return num
        }
    },
    components: {
        Loading,
        Star
    }
}
</script>

<style  lang="css">
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}
h2 {
    margin: 0;
} 
p {
    margin: 0;
} 
.movie-list {
    padding-top: 20px;
}
.movie-list .item {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    font-size: 14px;
    padding: 0 20px 20px 20px;
}
.movie-list .item .pic {
    display: inline-block;
    width: 110px; 
    height: 140px;
}
.movie-list .item .content {
    padding: 0 20px;
    display: inline-block;
    flex: 1;
    line-height: 20px;
    height: 140px;
    overflow: hidden;
    background: rgba(255,255,255,1);
}
.movie-list .item .content .name{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color:#000;
    font-size: 16px;
    margin: 5px 0;
}
.movie-list .item .content .desc{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    margin-bottom: 3px;
    font-size: 14px;
    color: #666;
}
.score p,.score div {
    display: inline-block;
    vertical-align: top;
}
</style>


