<template lang="html">
    <div class="search-box-wrapper">
        <search-box @query="searchData" ref="searchBox" @clearQuery="clearQuery"
        
        ></search-box>
        
        <suggest :searchList="searchList" 
        :query="query" ref="suggest" 
        @searchMoreResult="searchMoreResult" 
        :hasMore="hasMore"
        @listScroll="listScroll"
        @selectItem="selectItem" ></suggest>
        
        <div class="tag-list">
            <div class="movie-tag tag">
                <span v-for="item in movieTag" class="tag-item" @click="tagQuery(item)">{{item}}</span>
            </div>
            <div class="country-tag tag">
                <span v-for="item in countryTag" class="tag-item" @click="tagQuery(item)">{{item}}</span>
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
import SearchBox from 'base/search-box/search-box'
import { getSearch } from 'api/getSearch'
import Suggest from 'base/suggest/suggest'
import {mapMutations, mapGetters} from 'vuex'
export default {
    data() {
      return {
        query: '',
        movieTag: ['剧情', '爱情', '喜剧', '科幻', '动作', '悬疑', '治愈', '青春', '文艺'],
        countryTag: ['日本', '韩国'],
        tagSearch: false,
        beforeScroll: true,
        searchList: [],
        start: 0,
        hasMore: true,
        total: 0
      }
    },
    created() {
        // console.log(this.firstRouter)
    },
    computed: {
        ...mapGetters(['firstRouter'])
    },
    methods: {
        // 滚动失去焦点
        listScroll() {
            this.$refs.searchBox.blur()
        },
        // 把数据清空
        clearQuery() {
            this.searchList = []
            this.hasMore = true
            this.start = 0
        },
        tagQuery(query) {
             this.query = query
             this.$refs.searchBox.setQuery(query)
        },
        searchData(query) {
            this.start = 0
            this.query = query
            if(query == '') {
                this.searchShow = false
                return
            }
            getSearch(this.start, query).then((res) => {               
                this.searchList = res.subjects
                this.total = res.total
                this.start = this.start + res.count
            })
        },
        searchMoreResult() {
            if(this.start >= this.total) {
                this.hasMore = false
                return
            }
            getSearch(this.start, this.query).then((res) => {
                this.searchList = this.searchList.concat(res.subjects)
                this.start = this.start + res.count
                 console.log(res,this.start)
            })
        },
        // 调电影详情
        selectItem(id) {
            setTimeout(() => {
                this.$router.push({
                    path: `/search/${id}`
                 })
            },500)
            this.setFirstRouter('search')
        },
        ...mapMutations({
            setFirstRouter: 'SET_FIRST_ROUTER'
        })
    },
    beforeRouteLeave (to, from, next) {
        this.searchList = []
        this.start = 0
        this.query = ''
        this.$refs.searchBox.setQuery()
        next()
    },
    components: {
        SearchBox,
        Suggest
    }
}
</script>

<style lang="css">
.search-box-wrapper {
    background: #ccc;
    position: fixed;
    width: 100%;
    top: 88px;
    bottom: 0;
}
.tag-list {
    display: border-box;
    padding: 0 15px;
    width: 100%;
    background: #ccc;
}
         
.tag {
    padding: 10px 0;
}

.tag.movie-tag .tag-item{
     margin-bottom: 15px;
} 
.tag.country-tag {
    margin-top: -15px;
}
.tag-item {
    background: #7EC0EE;
    display: inline-block;
    margin-right: 15px;
    padding: 8px 16px;
    font-size: 14px;
    color: #000;
    border: 1px solid #fff;
    border-radius: 10px;
}
          
</style>


