<template>
 <!-- 影人，影人的照片，包括影人的作品 -->
    <div class="actors-wrapper" @click.stop  v-if="actorAll.length&&actorAll[0].avatars">
        <h3>{{title}}</h3> 
        <scroll :data="actorAll" :scrollX="true" :scrollY="false" :click="false" ref="scroll">
            <div class="actors-scroll-wrapper"  :style="getScrollWidth"> 
                <div class="actor" v-for="actor in actorAll"   @click.stop="selectItem(actor)">
                    <img v-lazy="actorAll.length&&actor.avatars ? actor.avatars.medium : ''" height="160" width="120">
                    <p class="actor-name-text">{{actor.name}}</p>
                </div>
            </div>
        </scroll>
    </div>
</template>

<script type="text/ecmascript6">
import Scroll from 'base/scroll/scroll'
const ACTOR_WIDTH = 160
export default {
    props: {
        actorAll: {
            type: Array,
            default: []
        },
        title: {
            type: String,
            default: '影人'
        }
    },
    computed: {
        getScrollWidth() {
            if(this.actorAll.length) {
                let w = ACTOR_WIDTH*this.actorAll.length
                return `width:${w}px`
            }
        }
    },
    methods: {
        selectItem(actor) {
            // console.log(2)
            this.$emit('select', actor)
            setTimeout(()=>{
                this.$refs.scroll.scrollTo(0,0)
            },1000)
            
        }
    },
    components: {
        Scroll
    }
}
</script>

<style lang="css">
.actors-wrapper {
    padding: 20px;
}
.actor {
    padding: 10px;
    display: inline-block;
    text-align: center;
    vertical-align: top;
}
.actor-name-text {
    width: 120px;
}
</style>




