
import * as types from './mutation-types'
const matutaions = {
  [types.SET_DETAIL_LIST](state, detailList) {
    state.detailList = detailList
  },
  [types.SET_ACTOR_DETAIL](state, actorDetail) {
    state.actorDetail = actorDetail
  },
  [types.SET_REVIEW_DETAIL](state, reviewDetail) {
    state.reviewDetail = reviewDetail
  },
  [types.CURRENT_DISCUSSION_TYPE](state, currentDiscussionType) {
    state.currentDiscussionType = currentDiscussionType
  },
  [types.SET_FIRST_ROUTER](state, firstRouter) {
    state.firstRouter = firstRouter
  }
}

// 暴露出去
export default matutaions


