<!--搜索容器，search页面里面引用。里面要用要到suggest组件-->
<template>
  <div class="search-box">
    <span class="icon-search"></span>
    <input ref="query" class="box" v-model="query" :placeholder="placeholder" />
    <span @click="clear" v-show="query" class="icon-dismiss">×</span>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      placeholder: {
        type: String,
        default: '搜索演员、电影'
      }
    },
    // query用来判断后面的小叉号是否出现。最好把前后空格去了
    data() {
      return {
        query: '',
        timer: 0
      }
    },
    // 置空输入内容
    methods: {
      clear() {
        this.query = ''
        this.$emit('clearQuery')
      },
     //   给外面用的.设置value
      setQuery(query) {
        this.query = query
      },
    //   给外面用的
      blur() {
        this.$refs.query.blur()
      }
      
    },
    watch: {
        query() {
            if(this.timer) {
                return
            }
            this.timer = setTimeout(() => {
                this.$emit('query', this.query)
                this.timer = 0
            },500)
        }
    }
  }
</script>

<style lang="css">
.search-box {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
    padding: 0 6px;
    height: 80px;
    background: #ccc;
    border-radius: 6px;
}


.search-box .icon-search {
    display: inline-block;
    vertical-align: top;
    height: 34px;
    width: 34px;
    background: url('search.jpg');
    background-size: 34px 34px;
}
    
.search-box .box {
    flex: 1;
    margin: 0 5px;
    line-height: 18px;
    background: #7EC0EE;
    color: #fff;
    font-size: 16px;
    height: 34px;
    border: none;
    margin: 0;
    border-radius: 0 10px 10px 0;
}
    
.search-box .box::placeholder {
    color: #fff;
}
    
.search-box .icon-dismiss {
    display: inline-block;
    height: 34px;
    width: 34px;
    color: #000;
    font: 34px/34px "宋体";
    /* background: url('close.jpg');
    background-size: 34px 34px; */
}
     
</style>


