<template>
<transition name="slide">
        <scroll :data="scrollData"  class="detail" ref="scroll"> 
             <div class="detail-scroll">
                <div class="back"  @click="back">
                    <span class="icon-back"><</span>
                </div>
                <div class="bg-image" :style="getBgImg" ref="bgImage"></div>
                <!-- 电影中间一栏,信息一栏 -->
                <div class="movie-news">
                    <div class="news-left">
                        <p>电影名：{{detailList.title}}</p>
                        <p>大陆上映时间：{{detailList.mainland_pubdate}}</p>
                        <p>分类：{{_arrayJoint(detailList.genres, '/')}}</p>
                        <p>国家：{{_arrayJoint(detailList.countries, '/')}}</p>
                        <p>时长:{{_arrayJoint(detailList.durations, '/')}}</p>
                    </div>
                    <!-- 评分框 -->
                    <div class="news-right">
                        <p>电影评分：{{detailList.rating ? detailList.rating.average : '0'}}</p>
                        <!-- 这里放个插件 -->
                        <star :score="Math.round(detailList.rating?detailList.rating.average: '0')"></star>
                        <p>{{detailList.collect_count}}人看过</p>
                    </div>
                </div>
                <!-- 电影简介 -->
                <div class="summary">
                    <h3>简介</h3>
                    <p>{{detailList.summary}}</p> 
                </div>
                <!-- 影人 -->
                <actors :actorAll="actorAll" @select="selectItem"></actors>
                <!-- 剧照 -->
                <stage-photo :stagePhotoAll="stagePhotoAll"></stage-photo>
                <!-- 评论 -->
                <div class="switch">
                    <switches :switches="switches" :currentIndex="switchesIndex" @switch="switchItem"></switches>
                </div>
                <movie-comment :comments="comments" :commentNum="detail.comments_count ? detail.comments_count: 0" v-show="switchesIndex === 0" 
                @needAllComments="needAllComments"></movie-comment>
                <movie-review :reviews="reviews" v-if="switchesIndex === 1" :reviewNum="detail.reviews_count ? detail.reviews_count: 0" @selectReview="selectReview"
                @needAllReviews="needAllReviews"></movie-review>
            </div>
             <router-view></router-view> 
        </scroll>
</transition>
</template>

<script type="text/ecmascript-6">
import {arrayJoint} from 'common/js/util'
import Scroll from 'base/scroll/scroll'
import StagePhoto from 'base/stage-photo/stage-photo'
import Actors from 'base/actors/actors'
import {getDetail} from 'api/getDetail' 
import {getReview} from 'api/getReview'
import {mapMutations,mapGetters} from 'vuex'
import Star from 'base/star/star'
import Switches from 'base/switches/switches';
import MovieComment from 'base/movie-comment/movie-comment'
import MovieReview from 'base/movie-review/movie-review'
const ACTOR_WIDTH = 140
export default {
    data() {
        return {
            detailList: {},
            scrollData: [],
            detail: {},
            // 演员，包括导演
            actorAll: [],
            // 剧照
            stagePhotoAll: [],
            // 开关组件的标题
            switches: ['精彩短评','精彩影评'],
            // 短评，精彩影评的选择开关,
            switchesIndex: 0,
            // popular_comments评论
            comments: [],
            // 影评reviews
            reviews: [],
            timer: 0,
            movieId: ''
        }
    },
    computed: {
        // 取值
        // ...mapGetters(['detailList']),
        getBgImg() {
            // console.log(this.detailList)
            if(this.detailList.images) {
                 return `background-image:url(${this.detailList.images.large})`
            }
            return ''
        },
        getScrollWidth() {
            if(this.actorAll.length) {
                let w = ACTOR_WIDTH*this.actorAll.length
                return `width:${w}px`
            }
        }
    },
    mounted() {
       
    },
    created() {
        // 自己去请求数据
        getDetail(`subject/${this.$route.params.id}`).then((res) => {
            this.detailList = res
            this.getData()
            this.getScrollDate()
        })
    },
    methods: {
        back() {
            this.$router.back()
            this.$destroy()
        },
        _arrayJoint(arr, jointSymbol) {
            if(arr) {
                 return arrayJoint(arr, jointSymbol) 
            }
            return ''
        },
        getActor() {
            if(this.detailList.casts || this.detailList.directors) {
                let director = this.detailList.directors
                let actor = this.detailList.casts
                // 去重。
                let isRepetiton = false
                actor.forEach((value,index) => {
                    if(value.name == director[0].name) {
                        isRepetiton = true
                    }
                })
                // 去重
                if(isRepetiton) {
                    this.actorAll = actor
                } else {
                     this.actorAll = director.concat(actor)
                }
            }
        },
        // 读照片的数据
        getStagePhotoAll() {
            if(this.detailList.trailers) {
                this.stagePhotoAll = this.detailList.trailers
            }
        },
        selectItem(actor) {
            // 获取演员详细信息
            getDetail(`celebrity/${actor.id}`).then((res) => {
                this.setActorDetail(res)
                this.$router.push({
                    path: `/celebrity/${actor.id}`
                })
            })  
        },
        ...mapMutations({
            setActorDetail: 'SET_ACTOR_DETAIL',
            setReviewDetail: 'SET_REVIEW_DETAIL',
            currentDiscussionType: 'CURRENT_DISCUSSION_TYPE'
        }),
        // 影评短评切换
        switchItem(index) {
            this.switchesIndex = index
            this.getScrollDate()
        },
        // 调用具体影评
        selectReview(id) {
            this.$router.push({
                path: `/review/${id}`
            })
        },
        getData() {
            this.detail = this.detailList
            this.comments = 
            this.detailList ? this.detailList.popular_comments : []
            this.reviews = this.detailList ? this.detailList.popular_reviews : []
            this.getActor()
            this.getStagePhotoAll()
            
            this.$refs.scroll.refresh() 
        },
        getScrollDate() {
            if(this.switchesIndex === 0) {
                this.scrollData = this.actorAll.concat(this.comments)
            }
             if(this.switchesIndex === 1) {
                this.scrollData = this.actorAll.concat(this.reviews)
            }
        },
        // 调全部影评
        needAllComments() {
            this.currentDiscussionType('comments')
            this.$router.push({
                path: `/discussion/${this.detail.id}`
            })
        },
        // 调全部短评
        needAllReviews() {
            this.currentDiscussionType('reviews')
            this.$router.push({
                path: `/discussion/${this.detail.id}`
            })
        }
    },
    watch: {
        scrollData() {
            this.$refs.scroll.refresh()   
        }
    },
    components: {
        Scroll,
        StagePhoto,
        Actors,
        Star,
        Switches,
        MovieComment,
        MovieReview
    }
}
</script>

<style lang="css">
h3 {
    margin: 0;
    color: green;
}
ul {
    list-style: none;
}
.detail {
   position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #ccc;
}
.detail-scroll {
    position: relative;
}
.back {
    height: 50px;
    width: 50px;
    position: absolute;
    top: 0;
    left: 10px;
    z-index: 50;
    font: bolder 40px/60px "宋体";
    text-align: center;
    line-height: 50px;
    color: #fff;
}

.bg-image {
    position: relative;
    width: 100%;
    height: 18rem;
    background: #666;
    background-position: center center;
     background-size: 12rem 16rem; 
    background-repeat: no-repeat;
}
.movie-news {
    position: relative;
    width: 100%;

}
.news-left {
    display: inline-block;
    vertical-align: middle;
    width: 60%;
    padding: 20px;
}
.news-right {
    display: inline-block;
    vertical-align: middle;
    box-sizing: border-box;
    width: 35%;
    padding: 15px;
    background: #fff;
}
.summary {
    padding: 10px 20px;
}
.actors-wrapper {
    padding: 20px;
}
.actor {
    padding: 10px;
    display: inline-block;
    text-align: center;
}
.slide-enter-active,.slide-leave-active {
     transition: all 0.6s;
}
   
.slide-enter,.slide-leave-to {
    transform: translate3d(100%, 0, 0);
}   
</style>


