<template lang="html">
  <scroll class="actor-detail" :data="actorDetail.photos" ref="scrollS">
      <div class="detail-scroll">
        <div class="back"  @click="back">
            <span class="icon-back"><</span>
        </div>
        <h3 class="actor-name">{{actorDetail.name}}</h3>
        <div class="actor-introduction">
          <img v-lazy="actorDetail.avatars.medium" height="160" with="120">
          <div class="introduction-text">
            <p>姓名：{{actorDetail.name_en ? actorDetail.name_en : actorDetail.name}}</p>
            <p>性别：{{actorDetail.gender}}</p>
            <p>生日:{{actorDetail.birthday}}</p>
            <p>出生地：{{actorDetail.born_place}}</p>
            <p>星座：{{actorDetail.constellation ? actorDetail.constellation : '未知'}}</p>
          </div>
        </div>
        <div class="slef-introduction" >
          <h3>个人介绍</h3>
          <p class="slef-text" :style="getHeight" ref="slefText">{{actorDetail.summary}}</p>
          <p class="more" ref="more" @click="showMore">{{showText}}</p>
        </div>
        <actor-works :works="actorDetail.works" @select="selectMovie"></actor-works>
      </div>
      <router-view></router-view>
  </scroll>
</template>

<script>
import Scroll from 'base/scroll/scroll'
import ActorWorks from 'base/actor-works/actor-works'
import {getDetail} from 'api/getDetail'
import {mapGetters, mapMutations} from 'vuex'
export default {
  data() {
    return {
      // 个人介绍是否收起
      showIntroduction: false,
      showText: '显示更多'
    }
  },
  computed: {
     ...mapGetters(['actorDetail','firstRouter']),
    getHeight() {
        if(this.showIntroduction) {
          this.showText = '收起'
          return `height:auto`
        } else {
          this.showText = '显示更多'
          return `height:20px`
        }
    }
   
  },
  methods: {
    back() {
      this.$router.push({
        path: `/${this.firstRouter}`
      })
      this.showIntroduction = false
      this.$destroy()
    },
    showMore() {
      this.showIntroduction = !this.showIntroduction
      setTimeout(() => {
         this.$refs.scrollS.refresh()
      },20)
    },
    selectMovie(movie) {
      
      this.$router.push({
          path: `/release-now/${movie.id}`
      })
      getDetail(`subject/${movie.id}`).then((res) => {
          console.log(res)
          this.setDetailList(res)
      })
    },
    ...mapMutations({
        setDetailList: 'SET_DETAIL_LIST'
    })
  },
  components: {
    Scroll,
    ActorWorks
  }
}
</script>

<style lang="css">
.actor-detail {
   position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #ccc;
}
.actor-detail .detail-scroll {
    position: relative;
    padding: 20px;
}
.actor-detail .back {
    height: 50px;
    width: 50px;
    position: absolute;
    top: 0;
    left: 10px;
    z-index: 50;
    font: bolder 40px/60px "宋体";
    line-height: 50px;
    text-align: center;
    color: #fff;
}
.actor-name {
   text-align: center;
}
.actor-introduction {
  margin-top: 20px;
  background: #333;
 
}
.introduction-text {
  display: inline-block;
  color: #fff;
  text-indent: 20px;
  vertical-align: middle;
}
.introduction-text p {
  width: 200px;
}
.slef-introduction {
  margin-top: 20px;
  padding-bottom: 20px;
  position: relative;
 
}
.slef-text {
   overflow: hidden;
}
.more {
  color: green;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>


